import React, { Component } from 'react';
import { Breadcrumb, Card, Tabs, Button } from 'antd';
import QianXi from '../../../common/components/Bmap/QianXi'

const TabPane = Tabs.TabPane;

class BMap extends Component {
    state = {
        datas: [],
        datas2: [],
    }
    getDatas = () => {
        const to = '';
        const datas = [
            { from: '', count: 354552, to: to },
            { from: '', count: 354551, to: to },
            { from: '', count: 97323, to: to },
            { from: '', count: 28664, to: to },
            { from: '', count: 16650, to: to },
            { from: '', count: 14379, to: to },
            { from: '', count: 10980, to: to },
            { from: '', count: 9603, to: to },
            { from: '', count: 4536, to: to },
            { from: '', count: 3556, to: to },
            { from: '', count: 2600, to: to },
        ]
        this.setState({datas})
    }
    getDatas2 = () => {
        const from = '';
        const datas2 = [
            { from: from, count: 354551, to: '' },
            { from: from, count: 85451, to: '' },
            { from: from, count: 97323, to: '' },
            { from: from, count: 28664, to: '' },
            { from: from, count: 16650, to: '' },
            { from: from, count: 14379, to: '' },
            { from: from, count: 10980, to: '' },
            { from: from, count: 9603, to: '' },
            { from: from, count: 4536, to: '' },
            { from: from, count: 3556, to: '' },
            { from: from, count: 2600, to: '' },
        ]
        this.setState({datas2})
    }
    render() {
        return (
            <div>
                <Breadcrumb  className="breadcrumb">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item>BMap</Breadcrumb.Item>
                </Breadcrumb>
                <div className="common-content">
                    <Card>
                        <Tabs defaultActiveKey="1">
                            <TabPane tab="" key="1">
                                <div style={{marginBottom:20}}>
                                    <Button onClick={this.getDatas}></Button>
                                </div>
                                <QianXi datas={this.state.datas || []}/>
                            </TabPane>
                            <TabPane tab="" key="2">
                                <div style={{marginBottom:20}}>
                                    <Button onClick={this.getDatas2}></Button>
                                </div>
                                <QianXi datas={this.state.datas2 || []}/>
                            </TabPane>
                        </Tabs>
                    </Card>
                </div>
            </div>
        )
    }
}

export default BMap;


